import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin', 'userId'])
	},
	data() {
		return {
			// isfirst:true,
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			showHeader: true,
			//个人信息,
			user: [],
			// 订单类型
			orderList: [{ text: '全部' }, { text: '待付款' }, { text: '待评价' }, { text: '退换货' }],
			// 工具栏列表
			mytoolbarList: [
				{ url: '../../user/keep/keep', text: '我的收藏', img: '/static/img/user/point.png' },
				{ url: '../../user/cart/cart', text: '购物车', img: '/static/img/user/quan.png' },
				{ url: '', text: '新客豪礼', img: '/static/img/user/renw.png' },
				{ url: '', text: '领红包', img: '/static/img/user/momey.png' },

				{ url: '', text: '去过景点', img: '/static/img/user/addr.png' },
				{ url: '', text: '账户安全', img: '/static/img/user/security.png' },
				{ url: '', text: '银行卡', img: '/static/img/user/bank.png' },
				{ url: '', text: '抽奖', img: '/static/img/user/choujiang.png' }
			]
		};
	},
	onLoad() {
		this.statusHeight = 0;




	},
	onReady() {
		//此处，演示,每次页面初次渲染都把登录状态重置
		console.log('onReady' + this);
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function() {},
			fail: function(e) {}
		});
	},
	onShow() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: res => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						uni.navigateTo({
							url: '../../login/login'
						});
					}
				}
			});
		} else {
			let that = this;
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/user/by/id',
				method: 'POST',
				data: {
					id: that.userId
				},
				success: res => {
					console.log(res);
					that.user = res.data.data;
				},
				fail: () => {},
				complete: () => {}
			});
			console.log(this.user);
		}
	},
	methods: {
		//登录
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../../login/login'
			});
		},
		bindLogout() {
			this.logout();
			try {
				uni.clearStorageSync();
			} catch (e) {
				// error
			}
			this.user = '';
			this.$store.dispatch('bindLogout');
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (!this.hasLogin) {
				uni.switchTab({
					url: '/pages/index/index'
				});
			}
		},

		//订单列表
		toOrderList(index) {
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
							uni.navigateTo({
								url: '../../login/login'
							});
						}
					}
				});
			} else {
				uni.navigateTo({ url: '../../user/order_list/order_list?tbIndex=' + (index - 1) });
			}
		},
		toSetting() {
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
							if (this.forcedLogin) {
								uni.navigateTo({
									url: '../../login/login'
								});
							}
						}
					}
				});
			} else {
				uni.navigateTo({ url: '../setting/setting' });
			}
		},
		isLogin() {
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false;
		},
		//充值
		toDeposit() {
			let that = this;
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
							uni.navigateTo({
								url: '../../login/login'
							});
						}
					}
				});
			} else {
				uni.navigateTo({
					url: '../../user/deposit/deposit?lastMoney=' + that.user.lastMoney,
					success: function() {
						console.log(that.user.lastMoney);
					}
				});
			}
		},
		toPage(url) {
			if (!url) {
				uni.showToast({ title: '还未包含此页面', icon: 'none' });
				return;
			}
			uni.navigateTo({
				url: url
			});
		}
	}
};
